.App {
  width: 400px;
  height: 520px;
  text-align: center;
  /* border: black 1px solid; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.result-area {
  height: 375px;
  margin-top: 20px;
  margin-left: 3%;
  overflow-y: auto;

  /* padding-right: 20px;
  box-sizing: content-box; */
}

.result-area::-webkit-scrollbar {
  width: 20px;
}

.result-area::-webkit-scrollbar-track {
  background-color: transparent;
}

.result-area::-webkit-scrollbar-thumb {
  border-radius: 20px;
  border: 6px solid transparent;
  background-clip: content-box;
}

.result-area:not(:hover)::-webkit-scrollbar-thumb {
  background-color: transparent;
}

.result-area:hover::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
}

.result-area::-webkit-scrollbar-thumb:hover {
  background-color:#C7C9CC;
}

.result-area:hover::-webkit-scrollbar-thumb:hover {
  background-color:#C7C9CC;
}

.search-bar {
  width: 90%;
  padding: 10px;
  margin: auto;
  border: 2px solid #266df1;
  border-radius: 10px;
  font-size: 14px;
}

h1 {
  margin-top: 0px;
  color: rgb(16, 55, 135);
  text-align: center;
}

.search-result {
  width: 90%;
  text-align: left;
  margin-bottom: 5px;
}

.result-title {
  font-size: 175%;
  margin-bottom: 5px;
  word-wrap: break-word;
}

.result-detail {
  margin-top: 5px;
  margin-bottom: 5px;

  color: #666666
}

.result-button-jump {
  margin-top: 5px;
  margin-bottom: 15px;

  width: 180px;
  height: 18px;
  line-height: 18px;
  padding: 10px 20px;

  background-color: white;
  color: black;
  border: #ddd 2px solid;

  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
  font-size: 100%;
}

.result-button-jump:hover {
  background-color: azure;
}

::placeholder {
  color: #bcceef;
}